.weather {
	width: 100%;
	height: 100%;
	padding-bottom: 5px;
	display: grid;
	grid-template-rows: 70px 1fr;
	gap: 5px;
	&_header {
		display: flex;
		align-items: center;
		justify-content: center;
		gap: 10px;
		grid-row: 1;
	}
	&_container {
		overflow: hidden;
	}
	&_forecast {
		display: flex;
		height: 100%;
		transition: 0.2s ease-in;
	}
}
.main {
	width: 100%;
	max-height: 100svh;
	display: grid;
	grid-template-columns: 50px 1fr 50px;
	grid-template-rows: 1fr 20px;
	gap: 5px;
}
.pagination-dots {
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	gap: 10px;
	grid-column: 2;
	grid-row: 2;
	&_dot {
		width: 10px;
		height: 10px;
		border-radius: 50%;
		background-color: var(--var-tertiary-color);
		transition: 0.2s ease-in;
		&.active-dot {
			background-color: var(--var-tertiary-color-hover);
		}
	}
}
